.Article {

    .container {
        width: 1224px;
        display: flex;
        flex-direction: column;
        margin-left: auto;
        margin-right: auto;
        position: relative;

        @include mq() {
            width: 100%;
        }
    }

    &-title {
        margin-left: auto;
        margin-right: auto;
        font-family: "Quicksand";
        color: #80BC00;
        margin-bottom: 70px;

        @include mq() {
            margin-bottom: 10px;
        }
    }

    &-box {
        background-color: rgba(#80BC00, .1);
        color: #80BC00;
        height: 210px;
        width: 690px;
        border-radius: 30px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
        text-align: center;

        @include mq() {
            display: flex;
            margin-left: auto;
            margin-right: auto;
            width: 350px;
            position: relative;
            left: 0;
            margin-top: 100px;
        }

        &-text {
            width: 600px;
            height: 125px;
            margin-left: auto;
            margin-right: auto;
            position: absolute;

            @include mq() {
                font-size: 18px;
                display: flex;
                align-items: center;
                width: 350px;
            }
        }
    }
}

.decaration {
    position: relative;
    display: flex;
    flex-direction: column;
}

.box-2,
.box-4,
.box-6,
.box-8 {
    margin-right: 0;
    margin-left: auto;
}

.Article-box {
    margin-top: 100px;
}

.panel {
    height: 300px;
    width: 380px;

    @include mq() {
        display: none;
    }
}

.panel1,
.panel3,
.panel5,
.panel7 {
    position: relative;
    top: 216px;
}