.Earnings {
    .container {
        width: 900px;
        margin-left: auto;
        margin-right: auto;

        @include mq() {
            width: 400px;
        }
    }

    &-list {
        display: flex;
        align-items: center;
        justify-content: center;

        li {
            margin-top: 35px;
            color: #737984;
            font-size: 18px;
        }
    }

    &-title {
        color: #80BC00;
        display: flex;
        justify-content: center;
        margin-top: 150px;
        margin-bottom: 70px;

        @include mq() {
            margin-top: 40px;
            margin-bottom: 80px;
        }
    }

    &-charge {
        height: 220px;
        width: 140px;
        border: 2px solid #FDDF4D;
        border-radius: 30px;
        display: flex;
        align-items: end;
        position: relative;
        margin-left: 50px;
        margin-right: 50px;

        &-box-1 {
            position: relative;

            select {
                width: 100px;
                appearance: none;
                border: none;
                border-bottom: 1px solid #80BC00;
                margin-bottom: 10px;
                display: flex;
                margin-left: auto;
                margin-right: auto;

                @include mq() {
                    margin-bottom: 30px;
                }
            }
        }

        &-button {
            margin-bottom: 10px;
            display: flex;
            align-items: center;
            justify-content: center;
            margin-left: auto;
            margin-right: auto;
            background-color: #80BC00;
            color: white;
            border: none;
            width: 114px;
            height: 35px;
            border-radius: 10px;

            @include mq() {
                height: 26px;
                width: 75px;
            }
        }

        &-title {
            color: #80BC00;
            text-align: center;

            @include mq() {
                margin-bottom: 30px;
            }
        }
    }
}

.icon-arrow {
    height: 15px;
    width: 15px;
    position: absolute;
    left: 105px;
}

.simsek {
    color: rgba(#FCD100, .7);
    height: 100px;
    width: 70px;
    position: absolute;
    bottom: 160px;
    left: 30px;

    @include mq() {
        position: absolute;
        left: 20px;
    }
}

.center {
    display: flex;
    justify-content: center;
}

.note {
    position: relative;
    margin-top: 100px;
    display: flex;
    justify-content: center;

}

.card {
    height: 100px;
    width: 400px;
    background-color: #7B98CE;
    border-radius: 10px;
    display: flex;
    justify-content: center;
    align-items: center;

    &-text {
        width: 260px;
        line-height: 25px;
        color: white;
    }
}

.yaprak {
    position: absolute;
    height: 247px;
    width: 156px;
    right: 120px;
    z-index: -1;
    top: 10px;
}

.yıldırım {
    position: absolute;
    width: 146px;
    height: 245px;
    right: 600px;

    z-index: -1;
}

.footer {
    margin-top: 100px;
    width: 100%;
    display: flex;
    background-color: #2D2D2D;

    .container {
        width: 1200px;
        height: 300px;
        display: flex;
        align-items: center;
        margin-left: auto;
        margin-right: auto;
        position: relative;

        @include mq() {
            display: flex;
            justify-content: center;
            flex-direction: column;
        }
    }

    .hy-logo {
        border-right: 2px solid #fff;
        margin-right: 30px;
    }

    .a-link {
        display: flex;
        flex-direction: column;
        margin-right: 0;
        margin-left: auto;

        @include mq() {
            display: flex;
            flex-direction: row;
            margin-top: 20px;
            text-align: center;
        }
    }

    .link {
        color: #fff;
        text-decoration: none;
        display: flex;
        align-items: center;
    }

}