.Header {
    background: url("/img/arkaplan.jpg")center center no-repeat;
    background-size: cover;
    width: 100%;
    height: 100vh;

    &-logo {
        position: relative;
        top: 30px;
        left: 50px;
    }

    &-title {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        margin-left: auto;
        margin-right: auto;

        @include mq() {
            display: flex;
            align-items: center;
            height: 554px;
        }

        &-frame {

            @include mq() {
                height: 50%;
                width: 50%;
            }
        }
    }

    &-arrow {
        display: flex;
        justify-content: center;
        margin-top: 60px;
    }
}


.Section {
    margin-top: 100px;
    width: 100%;


    &-paragraph {
        width: 1000px;
        display: flex;
        flex-direction: column;
        margin-left: auto;
        margin-right: auto;
        text-align: center;
        line-height: 30px;
        font-style: 20px;

        @include mq() {
            max-width: 350px;
        }
    }

    &-video {
        padding-top: 230px;
        background: url("/img/Vector-1.png") no-repeat, url("/img/Vector.png") no-repeat;
        background-position-x: 1100px, 140px;
        background-position-y: 530px, 150px;
        display: flex;
        justify-content: center;
        align-items: center;
        padding-bottom: 75px;

        @include mq() {
            background: url("/img/Vector-1.png") no-repeat, url("/img/Vector.png") no-repeat;
            background-size: 120px;
            background-position-x: 520px, 30px;
            background-position-y: 240px, 40px;
        }

        @include mq() {
            display: flex;
            justify-content: center;
            padding-top: 100px;
        }

        &-foto {
            @include mq() {
                height: 300px;
                max-width: 80%;
            }
        }

        &-icon {
            height: 73px;
            width: 73px;
            position: absolute;
        }
    }
}